.container{
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 200px;
    background-color: #fff;
    width: 600px ;
    padding-bottom: 30px;
    border-radius: 10px;
    
}

.header{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 100px;
    margin-top: 30px;
}

.text{
   color: #3c009d;
   font-size: 48px;
   font-weight: 700;
}

.underline{
    width: 61px;
    height: 6px;
    background-color:#3c009d;
    border-radius: 9px ;
    margin-top: -60px;


}

.input{
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 25px;

}

.input{
    display: flex;
    align-items:center ;
    margin: auto;
    width: 480px;
    height: 80px;
    background: #eaeaea;
    border-radius: 6px;

}

.input img{
    margin-top: 10px;


}

.input input{
    height: 50px;
    width: 400px;
    background:transparent ;
    border: none;
    outline: none;
    color: #797979;
    font-size: 19px;
    margin-top: -40px;

}

.forgot-password{
    padding-left: 62px;
    margin-top: 27px;
    color: #797979;
    font-size: 18px;

}

.forgot-password span{
    color: #4c00b4;
    cursor: pointer;

}

.submit-container{
    display: flex;
    gap: 30px;
    margin: 60px auto;
}


.submit{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 59px;
    color: #fff;
    background : #4c00b4;
    border-radius: 50px;
    font-size: 19px;
    font-weight: 700;
    cursor: pointer;
}

.gray{
    background: #EAEAEA;
    color: #676767;
}
.input + .input {
    margin-top: 20px;
}

.inputs{
    margin-top: 15px;
}